import React, { useState, useEffect, useRef } from 'react'
import { useSelector } from 'react-redux'
import { RootState } from 'store'

import styled from 'styled-components'

import TextInput from '../TextInput'
import { getMessages } from '../dataProcessing'
import SelectDropdown from '../SelectDropdown'
import SummarySection from '../SummarySection'
import dayOfWeek from '../../assets/data/dayOfWeek.json'
import paymentDeducted from '../../assets/data/paymentDeducted.json'
import InputFieldContainer from '../Commons/InputFieldContainer'

import { getStartDate } from '../summarySectionProcessing'

import {
  BANKACCOUNT_PAYMENT_ACCOUNT_NAME,
  BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER,
  BANKACCOUNT_PAYMENT_BSB,
  BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION,
  BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION,
  BANKACCOUNT_AGREE_TERM_AND_CONDITION,
  BANKACCOUNT_PAYMENT_TAB_DATA
} from '../FormHighOrder/types/YourContributionField'

import {
  EMPLOYMENT_STATUS,
  EMPLOYMENT_STATUS_CASUAL
} from '../FormHighOrder/types/AboutYouField'
import ReactRef from 'components/FormHighOrder/types/ReactRef'

export type Props = {
  onBankAccountTabValidated?: (wasSuccessful: boolean) => void
  onBankAccountData?: (data: any) => void
}

const BankAccountContainer = styled.div`
  animation-name: flickity;
  animation-duration: 1s;  
  animation-fill-mode: forwards;
  @keyframes flickity {
    0% {
      opacity:0;
    }
    100% {
      opacity:1;
    }
  };
  margin-top: 2.5rem;
  @media (max-width: 1024px) {
    margin-left: 0;
  }
`

const BankAccountForm = styled.div`
    width: 628px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: 1024px) {
        display: block;
        width: 100%;
    }
`

const Wrapper2 = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 628px;
    margin-top: 2.5rem;
    @media (max-width: 1024px) {
      width: 100%;
      display: grid;
      grid-template-columns: 100%;
      grid-row-gap: 2.1875rem;
    }
`

const BankAccount: React.FC<Props> = ({
  onBankAccountTabValidated,
  onBankAccountData
}) => {
  const getPrefetchData = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[3].data[BANKACCOUNT_PAYMENT_TAB_DATA])

  const getPrefetchDataForm2 = useSelector((state: RootState) => 
    state.FormHighOrder.pageData[1].data
  )

  const checkFetchData = (nameParam: string) => {
    return getPrefetchData !== undefined && getPrefetchData[nameParam]?.value !== ""
  }

  const [isValidAccountName, setIsValidAccountName] = useState(false)
  const [isValidAccountNumber, setIsValidAccountNumber] = useState(false)
  const [isValidBSB, setIsValidBSB] = useState(false)
  const [isValidDayOfDeduction, setIsValidDayOfDeduction] = useState(false)
  const [isValidFrequencyOfDeduction, setIsValidFrequencyOfDeduction] = useState(false)
  const [summaryDebitAmount, setSummaryDebitAmount] = useState('')
  const [summaryStartDate, setSummaryStartDate] = useState('')
  const [summaryFrequency, setSummaryFrequency] = useState('')

  const [isValidAgreeTermAndCondition, setIsValidAgreeTermAndCondition] = useState(
    getPrefetchData[BANKACCOUNT_AGREE_TERM_AND_CONDITION]?.value === true
  )

  const Refs: ReactRef = {
    [BANKACCOUNT_PAYMENT_ACCOUNT_NAME]: useRef<HTMLInputElement>(null),
    [BANKACCOUNT_PAYMENT_BSB]: useRef<HTMLInputElement>(null),
    [BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER]: useRef<HTMLInputElement>(null),
    [BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION]: useRef<HTMLInputElement>(null),
    [BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION]: useRef<HTMLInputElement>(null),
  }

  useEffect(() => {
    if(
      getPrefetchDataForm2[EMPLOYMENT_STATUS].value === 'Full Time'
      || getPrefetchDataForm2[EMPLOYMENT_STATUS].value === 'Salaried'
      || getPrefetchDataForm2[EMPLOYMENT_STATUS_CASUAL].value === '20+ hours'
    ) {
      setSummaryDebitAmount('$20.40')
    } else if (
      getPrefetchDataForm2[EMPLOYMENT_STATUS_CASUAL].value === '10-19 hours'
    ) {
      setSummaryDebitAmount('$14.60')
    } else if (
      getPrefetchDataForm2[EMPLOYMENT_STATUS_CASUAL].value === '0-9 hours'
    ) {
      setSummaryDebitAmount('$8.20')
    }
  }),[getPrefetchDataForm2[EMPLOYMENT_STATUS].value,getPrefetchDataForm2[EMPLOYMENT_STATUS_CASUAL].value]

  useEffect(() => {
    const isValidated = isValidAccountName && isValidAccountNumber && isValidBSB
      && isValidDayOfDeduction
      && isValidFrequencyOfDeduction
      && isValidAgreeTermAndCondition

    onBankAccountTabValidated ? onBankAccountTabValidated(isValidated) : {}

    onBankAccountData ? onBankAccountData({
      [BANKACCOUNT_PAYMENT_ACCOUNT_NAME]: {
        isValidated: true,
        value: Refs[BANKACCOUNT_PAYMENT_ACCOUNT_NAME].current?.value,
      },
      [BANKACCOUNT_PAYMENT_BSB]: {
        isValidated: true,
        value: Refs[BANKACCOUNT_PAYMENT_BSB].current?.value,
      },
      [BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER]: {
        isValidated: true,
        value: Refs[BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER].current?.value,
      },
      [BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION]: {
        isValidated: true,
        value: Refs[BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION].current?.value,
      },
      [BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION]: {
        isValidated: true,
        value: Refs[BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION].current?.value,
      },
      [BANKACCOUNT_AGREE_TERM_AND_CONDITION]: {
        isValidated: true,
        value: "true",
      },

    }) : {}
  }, [isValidAccountName, isValidAccountNumber, isValidBSB,
    isValidDayOfDeduction, isValidFrequencyOfDeduction,
    isValidAgreeTermAndCondition])

  const updateValidation = (nameParam: string, wasSuccessful: boolean) => {
    switch (nameParam) {
      case BANKACCOUNT_PAYMENT_ACCOUNT_NAME:
        setIsValidAccountName(wasSuccessful)
        return

      case BANKACCOUNT_PAYMENT_BSB:
        setIsValidBSB(wasSuccessful)
        return

      case BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER:
        setIsValidAccountNumber(wasSuccessful)
        return

      case BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION:
        setIsValidDayOfDeduction(wasSuccessful)
        return

      case BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION:
        setIsValidFrequencyOfDeduction(wasSuccessful)
        return

      case BANKACCOUNT_AGREE_TERM_AND_CONDITION:
        setIsValidAgreeTermAndCondition(wasSuccessful)
        return
    }
  }

  const fetchSummaryStartDate = getStartDate(summaryStartDate)


  const inputFieldFactory = (children: React.ReactNode) => {
    return (
      <InputFieldContainer>
        {children}
      </InputFieldContainer>)
  }

  const createInputField = (
    label: string,
    placeHolderParam: string,
    nameParam: string,
    maxLength?: number,
    widthSize?: string,
    tooltipName?: string,
    aRef?: React.RefObject<HTMLInputElement>,
  ): React.ReactNode => {
    return (
      <TextInput
        placeholder={placeHolderParam}
        name={nameParam}
        type="text"
        maxLength={maxLength}
        widthSize={widthSize}
        {...{ label, message: getMessages, tooltipName }}
        onInputSucessCallback={wasSuccessful => {
          updateValidation(nameParam, wasSuccessful)
        }}
        ref={aRef}
        inputFromProps={checkFetchData(nameParam) ?
          getPrefetchData[nameParam]?.value : ""}
        isValidated={getPrefetchData[nameParam]?.isValidated}
      />
    )
  }

  return (
    <BankAccountContainer>
      <BankAccountForm>
        {inputFieldFactory(
          createInputField("Account name",
            "Your account name",
            BANKACCOUNT_PAYMENT_ACCOUNT_NAME, 100,
            "36.625rem",
            undefined,
            Refs[BANKACCOUNT_PAYMENT_ACCOUNT_NAME]
          ),

        )}

        {inputFieldFactory(
          createInputField("BSB",
            "_ _ _ - _ _ _",
            BANKACCOUNT_PAYMENT_BSB, 9,
            undefined,
            undefined,
            Refs[BANKACCOUNT_PAYMENT_BSB]
          )
        )}

        {inputFieldFactory(
          createInputField("Account number",
            "_ _ _ _  _ _ _ _",
            BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER, 9,
            undefined,
            undefined,
            Refs[BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER])
        )}

        <Wrapper2>
          <SelectDropdown
            label="Which day of the week would you like your payments to be deducted?"
            name={BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION}
            contentDefault="Select day"
            data={dayOfWeek}
            onSelectedItemProp={wasSelected => {
              updateValidation(BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION, wasSelected)
            }}
            onSelectedValueProp={value => {
              setSummaryStartDate(value)
            }}
            ref={Refs[BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION]}
            inputFromProps={checkFetchData(BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION) ?
              getPrefetchData[BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION]?.value : undefined}
            isValidated={getPrefetchData[BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION]?.isValidated}
          />

          <SelectDropdown
            label="How often would you like your payments to be deducted?"
            name={BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION}
            contentDefault="Select frequency"
            data={paymentDeducted}
            onSelectedItemProp={wasSelected => {
              updateValidation(BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION, wasSelected)
            }}
            onSelectedValueProp={value => {
              setSummaryFrequency(value)
            }}
            ref={Refs[BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION]}
            inputFromProps={checkFetchData(BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION) ?
              getPrefetchData[BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION]?.value : undefined}
            isValidated={getPrefetchData[BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION]?.isValidated}
          />
        </Wrapper2>

      </BankAccountForm>

      <SummarySection
        paymentAuthorisationContent={`By agreeing to this form, I / We authorise Ezidebit, acting on behalf of the Business, to debit payments from my specified Credit Card above, and I / we acknowledge that EZI*SDA UNION will appear as the merchant on my credit card statement. Furthermore, I / we agree to reimburse and indemnify Ezidebit for any successful claims made by the Card Holder through their financial institution against Ezidebit.`}
        paymentAgreementProp={`I/We hereby authorise Global Payments Australia 1 Pty Ltd ACN 601 396 543 (Direct Debit User ID number 342190, 342191, 428198) (referred to as “Ezidebit”) to make periodic debits on behalf of the Business (referred to as “the Business”) as indicated on the attached Direct Debit Request which incorporates this DDR Service Agreement. I/We acknowledge that Ezidebit is acting as a Direct Debit Agent for the Business and that Ezidebit does not provide any goods or services (other than the direct debit collection services) to me/us for the Business pursuant to the Direct Debit Request and has no express or implied liability in relation to the goods and services provided or to be provided by the Business or the terms and conditions of any agreement that I/We have with the Business. I/We acknowledge that the debit amount will be debited from my/our nominated card or bank account according to the terms and conditions of my/our agreement with the Business and the terms and conditions of the Direct Debit Request (and specifically the Debit Arrangement including the Fees/Charges in the Direct Debit Request). I/We acknowledge that the details of my/our nominated card or bank account should be verified (eg: against a recent card or bank statement) to ensure accuracy of the details provided and I/we will contact my/our financial institution if uncertain of the accuracy of these details. I/We acknowledge that is my/our responsibility to ensure that there are sufficient available/cleared funds in the nominated account by the due date to enable the direct debit to be honoured on the due date for the debit. Direct debits normally occur overnight, however transactions can take up to 3 banking business days depending on the financial institution. Accordingly, I/we acknowledge and agree that sufficient funds will remain in the nominated account until the debit amount has been debited from the account. If there are insufficient funds available, I/we agree that Ezidebit will not be responsible for any fees and charges that may be charged by either my/our or its financial institution. I/We acknowledge that there may be a delay in processing the debit if: 1. a payment request is received by Ezidebit after Ezidebit’s usual cut off time, being 3:00pm Qld time, Monday to Friday; 2. a payment request is received by Ezidebit on a day that is not a banking business day in Sydney, NSW and Melbourne, VIC; or 3. there is a public or bank holiday on the day when the debit transaction is due to be processed or on any of the following days until the debit is processed. Any payment that falls due on any of the above will be processed on the next business day. I/We authorise Ezidebit to vary the amount of the payments from time to time upon receiving instructions from the Business of a variation provided for within my/our agreement with the Business or as may be agreed by me/us and the Business. I/We do not require Ezidebit to notify me/us of the variation to the debit amount. I/We acknowledge that Ezidebit is to provide at least 14 days' notice if it proposes to vary any of the terms and conditions of the Direct Debit Request (including this DDR Service Agreement) including varying the Debit Arrangement. I/We will contact the Business if I/we wish to alter or defer the Debit Arrangement. I/We acknowledge that any request by me/us to stop or cancel the Debit Arrangement will be directed to the Business. I/We acknowledge that any dispute regarding a debit will be directed to the Business and/or Ezidebit. If no resolution is forthcoming, I/we will contact my/our financial institution. I/We acknowledge that if a debit is returned by my/our financial institution as unpaid, a failed payment fee (as referred to in the Debit Arrangement) may be payable by me/us to Ezidebit. I/We will also be responsible for any fees and charges applied by my/our financial institution for each unsuccessful debit attempt together with any collection fees, including but not limited to any solicitor fees and/or collection agent fee as may be incurred by Ezidebit. I/We authorise Ezidebit to attempt to re-process any unsuccessful payments as advised by the Business. I/We acknowledge that certain fees and charges (including setup, variation, SMS or processing fees) may apply to the Direct Debit Request and may be payable to Ezidebit and agree to pay those fees and charges to Ezidebit. "Ezidebit" may appear as the merchant for a payment from my/our credit card (including a debit or charge card). I/We acknowledge and agree that Ezidebit will not be liable for any disputed transactions resulting from the supply or non supply of goods and/or services and that all disputes will be directed to the Business (as Ezidebit is acting only as a Direct Debit Agent for the Business). The Transaction Fee for a debit to a Credit Card calculated as a percentage may be subject to a minimum amount. I/We appoint Ezidebit as my/our agent for the control, management and protection of my/our personal information (relating to the Business and this Direct Debit Request) which is disclosed to Ezidebit. I/We irrevocably authorise Ezidebit to take all necessary action (which Ezidebit deems necessary) to protect and/or correct, if required, my/our personal information, including (but not limited to) correcting account numbers and providing such information to relevant third parties and otherwise disclosing or allowing access to my/our personal information to third parties in accordance with the Ezidebit Privacy Policy. Other than as provided in this Direct Debit Request or the Ezidebit Privacy Policy, Ezidebit will keep your personal information about your nominated account private and confidential unless this information is required to investigate a claim made relating to an alleged incorrect or wrongful debit, to be referred to a debt collection agency for the purposes of debt collection or as otherwise required or permitted by law. The Ezidebit Privacy Policy can be found at http://www.ezidebit.com/au/privacy-policy/ I/We hereby irrevocably authorise, direct and instruct any third party who holds/stores my/our personal information (relating to the Business and this Direct Debit Request) to release and provide such information to Ezidebit. I/We authorise: 1. Ezidebit to verify with my/our financial institution and/or correct, if necessary, details of my/our account; and 2. My/our financial institution to release information allowing Ezidebit to verify my/our account details.`}
        summaryTableDataProp={[
          {
            title: "Debit amount:",
            value: summaryDebitAmount
          },

          {
            title: "Start date:",
            value: fetchSummaryStartDate
          },

          {
            title: "Frequency:",
            value: summaryFrequency
          },

          {
            title: "Duration:",
            value: ""
          },
        ]}
        noteOnPaymentProp={[
          "Your membership can be cancelled at any time",
          "Membership fees are tax-deductible",
          "Transactions will appear on your statement as ‘SDA UNION’"
        ]}
        enableToAgreeTermAndConditionProp={true}
        onSelectAgreeTermAndCondition={wasSelected => {
          updateValidation(BANKACCOUNT_AGREE_TERM_AND_CONDITION, wasSelected)
        }}
      />

    </BankAccountContainer>
  )
}

export default BankAccount
