import React from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSquare } from '@fortawesome/free-regular-svg-icons'
import { faCheck, faCheckDouble } from '@fortawesome/free-solid-svg-icons'

import { SummaryTable } from './SummaryTable'
import ValidationIndicator from '../ValidationIndicator'
import { NoteOfPayment } from './NoteOfPayment'
import { PaymentAgreement } from './PaymentAgreement'
import { PaymentAuthorisation } from './PaymentAuthorisation'
import HorizontalLine from '../HorizontalLine'
import color from '../color-css/color'
export type Props = {
    summaryTableDataProp?: TableData[]
    paymentAuthorisationContent?: string
    paymentAgreementProp?: string
    noteOnPaymentProp: string[]
    enableToAgreeTermAndConditionProp?: boolean
    onSelectAgreeTermAndCondition?: (wasSelected: boolean) => void
    inputFromProps?: string
    isValidated?: boolean
}

const SummarySectionContainer = styled.div`
    margin-top: 3.75rem;
    max-width: 40.9375rem;
    font-family: museo-sans, sans-serif;
    @media (max-width: 1024px) {
        max-width: 100%;
    }
`

const SummaryTitle = styled.h6`
    text-align: left;
    font: 700 1.25rem museo-sans, sans-serif;
    letter-spacing: -0.06px;
    color: ${color.blue_17_percent};
    opacity: 1;
    margin-top: 3.75rem;
`

const HorizontalLineSummarySection = styled(HorizontalLine)`
    margin-top: 3.5rem;
`

interface ContainerCheckboxProps {
    isCheck?: boolean,
    check: boolean
}
const ContainerCheckbox = styled.a<ContainerCheckboxProps>`
    max-width: 37.75rem;
    background-color:${props => props.check ? '#E2F1F8' : '#EFF0F0'} ;
    transition: background,border 0.5s ease-in-out;
    border: 1px solid ${color.white};
    border-radius: 0.625rem;
    opacity: 1;
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    align-items: center;
    margin-top: 2.5rem;
    padding: 1.25rem;
    cursor: pointer;
`

const ContainerCheckboxHover = styled.div`
    margin-bottom: 0.625rem;
    &:hover ${ContainerCheckbox} {
        border: 1px solid ${color.blue_darker};
        border-radius: 0.625rem;
        cursor: pointer;	
    }
`
const ContainerIcon = styled.div`
  width: 1.15rem;
  @media screen and (max-width: 1024px) {
    margin-left: 0.9375rem;
    width: 0.875rem;
  }
`
const StyledIcon = styled(FontAwesomeIcon)<ContainerCheckboxProps>`
  width: 1rem;
  animation-name: flickity;
  animation-duration: 1s;  
  animation-fill-mode: forwards;
  @keyframes flickity {
      0% {
          opacity:0;
      }
      100% {
          opacity:1;
      }
  };
  display: ${props => props.isCheck ? 'block' : 'none'};
  color: ${props => props.check ? '#ff6820' : '#757575'};
  text-align: center;
  @media screen and (max-width: 1024px) {
    width: 0.875rem;
  }
`
const StyledTitle = styled.span<ContainerCheckboxProps>`
    text-align: left;
    font: 500 1rem museo-sans, sans-serif;
    letter-spacing: -0.05px;
    color: ${props => props.check ? color.blue_17_percent : '#757575'};
    opacity: 1;
    margin-left: 1.35rem;
`

const SummarySection: React.FC<Props> = ({
  summaryTableDataProp,
  paymentAuthorisationContent,
  paymentAgreementProp,
  noteOnPaymentProp,
  enableToAgreeTermAndConditionProp,
  onSelectAgreeTermAndCondition,
  inputFromProps,
  isValidated
}) => {
  const [check, changeCheck] = React.useState<boolean>(false)
  React.useEffect(() => {
    if(inputFromProps && isValidated) {
      changeCheck(true)
    }
  }, [inputFromProps, isValidated])
  return (
    <SummarySectionContainer>
      <HorizontalLineSummarySection />
      <SummaryTitle>Summary</SummaryTitle>
      {
        summaryTableDataProp
                && summaryTableDataProp.length > 0
                && <SummaryTable tableData={summaryTableDataProp} />}

      {noteOnPaymentProp
                && noteOnPaymentProp.length > 0
                && <NoteOfPayment noteOnPayment={noteOnPaymentProp} />}

      {
        paymentAuthorisationContent !== undefined
                && paymentAuthorisationContent.length >= 0
                && (
                  <React.Fragment>
                    <HorizontalLineSummarySection />
                    <PaymentAuthorisation
                      paymentAuthorisationContent={paymentAuthorisationContent} />
                  </React.Fragment>
                )
      }
      {
        paymentAgreementProp
                && (
                  <PaymentAgreement
                    expandableParagraphIsLongTextProp={true}
                    expandableParagraphDescriptionTextProp={paymentAgreementProp}
                  />
                )}
      {
        enableToAgreeTermAndConditionProp
                && (
                  <ContainerCheckboxHover>
                    <ContainerCheckbox
                      check={check}
                      onClick={() => { 
                        const checkValue = !check
                        changeCheck(checkValue)
                        onSelectAgreeTermAndCondition ? onSelectAgreeTermAndCondition(checkValue) : {}
                      }}
                    >
                      <ContainerIcon>
                        <StyledIcon
                          isCheck={check ? true : false}
                          check={check}
                          icon={faCheck}
                        />
                        <StyledIcon
                          isCheck={check ? false : true}
                          check={check}
                          icon={faSquare}
                        />
                      </ContainerIcon>
                      <StyledTitle check={check}>
                            I agree to the Terms and Conditions of the Bank Account Request and Service Agreement *
                      </StyledTitle>
                    </ContainerCheckbox>
                  </ContainerCheckboxHover>
                )}
      {
        <ValidationIndicator
          isCorrect={check}
          message=""
          visibility={check} />
      }
    </SummarySectionContainer>
  )
}

export default SummarySection