export const handlePhoneNumber = (inputGetter: string, event: any) => {
  let phoneNumberStartWith02030708: any = renderPhoneNumberStrat1(inputGetter)
  let phoneNumberStartWith04: any = renderPhoneNumberStrat2(inputGetter)
  if (inputGetter.length === 3 && inputGetter.substring(0,2) === '02'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  else if (inputGetter.length === 8 && inputGetter.substring(0,2) === '02'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  if (inputGetter.length === 3 && inputGetter.substring(0,2) === '03'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  else if (inputGetter.length === 8 && inputGetter.substring(0,2) === '03'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  if (inputGetter.length === 3 && inputGetter.substring(0,2) === '07'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  else if (inputGetter.length === 8 && inputGetter.substring(0,2) === '07'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  if (inputGetter.length === 3 && inputGetter.substring(0,2) === '08'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  else if (inputGetter.length === 8 && inputGetter.substring(0,2) === '08'
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith02030708
  }
  else if (inputGetter.length === 5 && inputGetter.substring(0,2) === '04' 
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith04
  }
  else if (inputGetter.length === 9 && inputGetter.substring(0,2) === '04' 
    && Number(inputGetter.slice(-1).toString()) !== 0) {
      event.target.value = phoneNumberStartWith04
  }
  return event.target.value
}

export const handleDateOnDesktop = (inputGetter: string, event: any) => {
  let result = renderDate(inputGetter)
  if (inputGetter.length === 3 && Number(inputGetter.slice(-1).toString()) !== 0) {
    event.target.value = result
  }
  if (inputGetter.length === 4 && inputGetter.slice(-1) !== '/') {
    event.target.value = result
  }
  if (inputGetter.length === 5 && Number(inputGetter.slice(-1).toString()) !== 0) {
    event.target.value = result
  }
  if (inputGetter.length === 8 && Number(inputGetter.slice(-1).toString()) !== 0) {
    event.target.value = result
  }
  if (inputGetter.length === 9 && inputGetter.slice(-1) !== '/') {
    event.target.value = result
  }
  if (inputGetter.length === 10 && Number(inputGetter.slice(-1).toString()) !== 0) {
    event.target.value = result
  }
  return event.target.value
}

