export const handleStateOrTerritory = (input: string) => {
    switch (input) {
        case 'NSW':
            return 'New South Wales'
        case 'VIC':
            return 'Victoria'
        case 'QLD':
            return 'Queensland'
        case ' WA':
            return 'Western Australia'
        case 'TAS':
            return 'Tasmania'
        case 'ACT':
            return 'Australian Capital Territory'
        case ' NT':
            return 'Northern Territory'
        case ' SA':
            return 'South Australia'
    }
}