import React, { useState, useRef, useEffect } from 'react'
import styled from 'styled-components'

import { getMessages } from '../dataProcessing'
import TextInput from '../TextInput'
import HorizontalLine from '../HorizontalLine'
import {
  BILLING_FIRST_NAME,
  BILLING_LAST_NAME,
  BILLING_ADDRESS,
  BILLING_UNIT_NUMBER,
  BILLING_CITY,
  BILLING_STATE_OR_TERITORY,
  BILLING_POSTCODE,
  BILLING_ADDRESS_OPTION,
  DIFFERENT_BILLING_ADDRESS_DATA,
} from '../FormHighOrder/types/YourContributionField'
import { useSelector, useDispatch } from 'react-redux'
import { RootState } from 'store'
import InputFieldContainer from '../Commons/InputFieldContainer'

export type Props = {
  onBillingAddressDataValidated?: (wasSuccessful: boolean) => void,
  onDifferentBillingAddressData?: (data: any) => void
  inputFromProps?: string
  fetchDataFromProps?: (value: string) => void
}

const BillingAddressFieldsContainer = styled.div`
  animation-name: flickity;
  animation-duration: 1s;  
  animation-fill-mode: forwards;
  @keyframes flickity {
      0% {
          opacity:0;
      }
      100% {
          opacity:1;
      }
  };
  @media screen and (min-width: 1024px) {
    max-width: 655px;
  }
  @media (max-width: 768px) {
      margin-left: 0;
  };
`

const BillingAddressFieldsForm = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 654px;
  height: 100%;
  @media (max-width: 768px) {
      display: block;
      width: 100%;
  }
`

const BillingAddressFields: React.FC<Props> = ({
  onBillingAddressDataValidated,
  onDifferentBillingAddressData }) => {

  interface ReactRef {
    [label: string]: React.RefObject<HTMLInputElement>,
  }

  const [firstNameValidation, setFirstNameValidation] = useState(false)
  const [lastNameValidation, setLastNameValidation] = useState(false)
  const [billingAddressValidation, setBillingAddressValidation] = useState(false)
  const [billingUnitNumberValidation, setBillingUnitNumberValidation] = useState(false)
  const [billingCityValidation, setBillingCityValidation] = useState(false)
  const [billingStateValidation, setBillingStateValidation] = useState(false)
  const [billingPostCodeValidation, setBillingPostCodeValidation] = useState(false)

  const [isFormValidated, setIsFormValidated] = useState(false)

  const Refs: ReactRef = {
    [BILLING_FIRST_NAME]: useRef<HTMLInputElement>(null),
    [BILLING_LAST_NAME]: useRef<HTMLInputElement>(null),
    [BILLING_ADDRESS]: useRef<HTMLInputElement>(null),
    [BILLING_UNIT_NUMBER]: useRef<HTMLInputElement>(null),
    [BILLING_CITY]: useRef<HTMLInputElement>(null),
    [BILLING_STATE_OR_TERITORY]: useRef<HTMLInputElement>(null),
    [BILLING_POSTCODE]: useRef<HTMLInputElement>(null),
  }

  const getValidate = useSelector((state: RootState) => {
    const { data } = state.FormHighOrder.pageData[3]
    return data[DIFFERENT_BILLING_ADDRESS_DATA][BILLING_FIRST_NAME].isValidated
      && data[DIFFERENT_BILLING_ADDRESS_DATA][BILLING_LAST_NAME].isValidated
      && data[DIFFERENT_BILLING_ADDRESS_DATA][BILLING_ADDRESS].isValidated
      && data[DIFFERENT_BILLING_ADDRESS_DATA][BILLING_UNIT_NUMBER].isValidated
      && data[DIFFERENT_BILLING_ADDRESS_DATA][BILLING_CITY].isValidated
      && data[DIFFERENT_BILLING_ADDRESS_DATA][BILLING_STATE_OR_TERITORY].isValidated
      && data[DIFFERENT_BILLING_ADDRESS_DATA][BILLING_POSTCODE].isValidated
  })

  const getPrefetchData =
    useSelector((state: RootState) =>
      state.FormHighOrder.pageData[3].data[DIFFERENT_BILLING_ADDRESS_DATA])

  useEffect(() => {
    const isValidated = firstNameValidation && lastNameValidation && billingAddressValidation
      && billingUnitNumberValidation && billingCityValidation && billingStateValidation
      && billingPostCodeValidation
    setIsFormValidated(isValidated)

    onBillingAddressDataValidated ? onBillingAddressDataValidated(isValidated) : {}

  }), [
      getValidate,
      firstNameValidation, lastNameValidation,
      billingAddressValidation, billingUnitNumberValidation,
      billingCityValidation, billingStateValidation, billingPostCodeValidation
    ]


  useEffect(() => {
    if (isFormValidated && onDifferentBillingAddressData !== undefined) {

      onDifferentBillingAddressData({
        [BILLING_FIRST_NAME]: {
          value: Refs[BILLING_FIRST_NAME].current?.value,
          isValidated: true,
        },
        [BILLING_LAST_NAME]: {
          value: Refs[BILLING_LAST_NAME].current?.value,
          isValidated: true,
        },
        [BILLING_ADDRESS]: {
          value: Refs[BILLING_ADDRESS].current?.value,
          isValidated: true,
        },
        [BILLING_UNIT_NUMBER]: {
          value: Refs[BILLING_UNIT_NUMBER].current?.value,
          isValidated: true,
        },
        [BILLING_CITY]: {
          value: Refs[BILLING_CITY].current?.value,
          isValidated: true,
        },
        [BILLING_STATE_OR_TERITORY]: {
          value: Refs[BILLING_STATE_OR_TERITORY].current?.value,
          isValidated: true,
        },
        [BILLING_POSTCODE]: {
          value: Refs[BILLING_POSTCODE].current?.value,
          isValidated: true,
        },
      })
    }
  }, [isFormValidated])

  const checkFetchData = (nameParam: string) => {
    return getPrefetchData !== undefined && getPrefetchData[nameParam]?.value !== ""
  }

  const setValidation = (nameParam: string, wasAnInputSuccess: boolean) => {
    switch (nameParam) {
      case BILLING_FIRST_NAME:
        setFirstNameValidation(wasAnInputSuccess)
        return
      case BILLING_LAST_NAME:
        setLastNameValidation(wasAnInputSuccess)
        return
      case BILLING_ADDRESS:
        setBillingAddressValidation(wasAnInputSuccess)
        return
      case BILLING_UNIT_NUMBER:
        setBillingUnitNumberValidation(wasAnInputSuccess)
        return
      case BILLING_CITY:
        setBillingCityValidation(wasAnInputSuccess)
        return
      case BILLING_STATE_OR_TERITORY:
        setBillingStateValidation(wasAnInputSuccess)
        return
      case BILLING_POSTCODE:
        setBillingPostCodeValidation(wasAnInputSuccess)
        return
    }
  }

  const inputFieldFactory = (children: React.ReactNode) => {
    return (
      <InputFieldContainer>
        {children}
      </InputFieldContainer>)
  }

  const createInputField = (
    label: string,
    placeHolderParam: string,
    nameParam: string,
    maxLength?: number,
    labelNotRequire?: string,
    aRef?: React.RefObject<HTMLInputElement>,
    width?: string): React.ReactNode => {
    return (
      <TextInput
        placeholder={placeHolderParam}
        name={nameParam}
        type="text"
        maxLength={maxLength}
        {...{ label, message: getMessages, labelNotRequire }}
        onInputSucessCallback={wasAnInputSuccess => {
          setValidation(nameParam, wasAnInputSuccess)
        }}
        ref={aRef}
        widthSize={width}
        inputFromProps={checkFetchData(nameParam) ?
          getPrefetchData[nameParam]?.value : ""}
        isValidated={getPrefetchData[nameParam]?.isValidated}
      />
    )
  }

  return (
    <BillingAddressFieldsContainer>
      <BillingAddressFieldsForm>
        {inputFieldFactory(
          createInputField("First name", "Your first name", BILLING_FIRST_NAME,
            undefined, undefined, Refs[BILLING_FIRST_NAME])
        )}

        {inputFieldFactory(
          createInputField("Last name", "Your last name", BILLING_LAST_NAME,
            undefined, undefined, Refs[BILLING_LAST_NAME])
        )}

        {inputFieldFactory(
          createInputField("Billing address", "Type Address here", BILLING_ADDRESS,
            undefined, undefined, Refs[BILLING_ADDRESS], "37.75rem")
        )}

        {inputFieldFactory(
          createInputField("", "Type unit number", BILLING_UNIT_NUMBER,
            undefined, "Unit number (if applicable)", Refs[BILLING_UNIT_NUMBER])
        )}

        {inputFieldFactory(
          createInputField("City", "Type city", BILLING_CITY,
            undefined, undefined, Refs[BILLING_CITY])
        )}

        {inputFieldFactory(
          createInputField("State / territory", "Type state / territory", BILLING_STATE_OR_TERITORY,
            undefined, undefined, Refs[BILLING_STATE_OR_TERITORY])
        )}

        {inputFieldFactory(
          createInputField("Postcode", "Type postcode", BILLING_POSTCODE,
            4, undefined, Refs[BILLING_POSTCODE])
        )}

      </BillingAddressFieldsForm>
      <div style={{
        marginTop: `3.125rem`,
        marginBottom: `3.125rem`
      }}>
        <HorizontalLine />
      </div>

    </BillingAddressFieldsContainer >
  )
}

export default BillingAddressFields
