/**
 * For 02 03 07 08
 * render input as XX XXXX XXXX
 */
export const renderPhoneNumberStrat1 = (input: string) => {
    let array = input.split('')
    let result = ''
    if (input.startsWith('02') || input.startsWith('03') ||
      input.startsWith('07') || input.startsWith('08')) {
      if (input.length === 3) {
        array.splice(2, 0, ' ');
        result = array.join('')
        return result
      }
      else if (input.length === 8) {
        array.splice(7, 0, ' ');
        result = array.join('')
        return result
      }
      else if (input.length === 10) {
        return input.replace(/^(\d{2})(\d{4})(\d{4})$/g, '$1 $2 $3')
      }
      
      return input;
    }
    else return input
  }
  
  /**
   * For 04
   * render input as XXXX XXX XXX
   * @param input 
   */
  export const renderPhoneNumberStrat2 = (input: string) => {
    let array = input.split('')
    let result = ''
    if (input.startsWith('04')) {
      if (input.length === 5) {
        array.splice(4, 0, ' ');
        result = array.join('')
  
        return result
      }
      else if (input.length === 9) {
        array.splice(8, 0, ' ');
        result = array.join('')
        return result
      }
      else if (input.length === 10) {
        return input.replace(/^(\d{4})(\d{3})(\d{3})$/g, '$1 $2 $3')
      }
      return input;
    }
  }
  