import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faInfo } from '@fortawesome/free-solid-svg-icons'
import { isMobile } from 'react-device-detect'
import color from '../color-css/color'

const TooltipIconContainer = styled.div`
    width: 1.23rem;
    height: 1.23rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: ${color.grey_darker};
    transition: background 0.5s ease-in-out;
    border-radius: 50%;
    margin-left: 2px;
`

const StyledIcon = styled(FontAwesomeIcon)`
    color: ${color.white};
    width: 0.75rem !important;
    height: 0.625rem;
`
interface TooltipExpandableProps{
    visibility?: boolean
}

const TooltipExpandable = styled.div<TooltipExpandableProps>`
    opacity: ${props => props.visibility ? '1' : '0'};
    visibility: hidden;
    min-width: 13.625rem;
    position: relative;
    -moz-border-radius: 0.625rem;
    -webkit-border-radius: 0.625rem;
    border-radius: 0.625rem;
    background: ${color.white} 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 3.125rem #012F571A;
    top: 1.25rem;
    right: 1.25rem;
    z-index: 1;
    transition: .3s;
    transform: translateX(-10px);
    &:before {
        content: "";
        position: absolute;
        left: 1.25rem;
        top: -1.125rem;
        width: 0;
        height: 0;
        border-top: 1.25rem solid transparent;
        border-right: 1.625rem solid ${color.white};
        border-bottom: 0.8125rem solid transparent;
        transform: rotate(90deg);
        @media screen and (max-width: 768px) {
            display: flex;
            position: relative;
            margin: auto;
            margin-bottom: -15px;
        }
    }
    @media screen and (max-width: 768px) {
        right: 0;
    }
`

interface TooltipContainer {
    tooltipName: string,
    visibility: boolean,
    widthSize?: string,
    expandVisibility?: boolean
}
// TooltipContainer
const TooltipContainer = styled.div<TooltipContainer>`
    display: ${props => props.visibility === true ? "none" : "flex"};
    flex-wrap: wrap;
    min-width: 13.625rem;
    max-width: ${props => props.widthSize ? props.widthSize : "18.1875rem"};
    height: 1.25rem;
    border-radius: 50%;
    vertical-align: middle;
    cursor: pointer;	
    &:hover ${TooltipExpandable} {
        visibility: visible;
        opacity: 1;
        transform: translateX(0);
        transition: all .3s ease;
    }
    &:hover ${TooltipIconContainer} {
        background-color: ${color.tooltip_icon};
        transition: background 0.5s ease-in-out;
    }
    @media screen and (max-width: 768px) {
        max-width: 100%;
    }
`

const TooltipExpandableContent = styled.div`
    font-family: museo-sans, sans-serif;
    padding: 0.9375rem 1.1325rem 0.875rem 0.9375rem;
    text-align: left;
    font-weight: normal;
    font-size: 0.875rem;
    letter-spacing: -0.0025rem;
    color: ${color.grey_29_percent};
    @media screen and (max-width: 768px) {
        padding: 0 30px 21px 30px;
    }
    
`

interface TooltipMessage {
    tooltipName: string
}
const TooltipMessage = styled.span<TooltipMessage> `
    width: 12.5rem;
    font-family: museo-sans, sans-serif;
    font-style: italic;
    font-weight: 500;
    margin-left: 0.625rem;
    font-size: 1rem;
    margin-left: 0.4rem;
    cursor: pointer;
    color: ${color.grey_darker};
`

const Tooltip = (props: any) => {
    const [expandVisibility, setExpandVisibility] = useState(false);
    useEffect(() => {
        const closeExpand = () => {
            setExpandVisibility(false);
        };

        document.body.addEventListener('click', closeExpand);
        return () => {document.body.removeEventListener('click', closeExpand)}
    }, []);

    if (isMobile) {
        return (
            <div>
                <TooltipContainer
                    visibility={props.visibility}
                    tooltipName={props.tooltipName}
                >
                    <TooltipIconContainer>
                        <StyledIcon icon={faInfo} />
                    </TooltipIconContainer>
                    <TooltipMessage tooltipName={props.tooltipName}>
                        {props.tooltipTitle}
                    </TooltipMessage>
                    <TooltipExpandable>
                        <TooltipExpandableContent dangerouslySetInnerHTML={{
                            __html: props.tooltip_message
                        }}>
                        </TooltipExpandableContent>
                    </TooltipExpandable>
                </TooltipContainer>
            </div>
        )
    }
  
    return (
        <div>
            <TooltipContainer
                onClick={() => setExpandVisibility(true)}
                visibility={props.visibility}
                tooltipName={props.tooltipName}
                widthSize={props.widthSize}>
                <TooltipIconContainer>
                    <StyledIcon icon={faInfo} />
                </TooltipIconContainer>
                <TooltipMessage tooltipName={props.tooltipName}>
                    {props.tooltipTitle}
                </TooltipMessage>
                <TooltipExpandable visibility={expandVisibility}>
                    <TooltipExpandableContent dangerouslySetInnerHTML={{
                        __html: props.tooltip_message
                    }}>
                    </TooltipExpandableContent>
                </TooltipExpandable>
            </TooltipContainer>
        </div>
    )
}

export default Tooltip;