import React from 'react'
import Truncate from 'react-truncate'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus, faMinus } from '@fortawesome/free-solid-svg-icons'
import CustomScroll from 'react-custom-scroll'
interface StyledDesProps {
  lines: any
}
const StyledDes = styled(Truncate) <StyledDesProps>`
    text-align: left;
    font-size: 1rem;
    font-weight: 500;
    font-family: museo-sans, sans-serif;
    line-height: 1.75rem;
    letter-spacing: -0.05px;
    color: #4A4A4A;
    opacity: 1;
    line-height: 1.75rem;
    @media screen and (max-width: 768px) {
      font-size: 0.9375rem;
      line-height: 1.375rem;
    }

`
const StyledDesNoExpand = styled.div`
  text-align: left;
  font-size: 1rem;
  font-weight: 500;
  font-family: museo-sans, sans-serif;
  letter-spacing: -0.05px;
  color: #4A4A4A;
  opacity: 1;
  line-height: 1.75rem;
  @media screen and (max-width: 768px) {
    font-size: 0.9375rem;
    line-height: 1.375rem;
  }
`

const StyledIcon = styled(FontAwesomeIcon)`
  color: #FF6820;
  width: 0.75rem !important;
  text-align: center;
  @media screen and (max-width: 768px) {
    width: 0.625rem !important;
  }
`
const StyledExpand = styled.a`
  text-align: right;
  text-decoration: underline;
  font-size: 1rem;
  font-weight: 500;
  font-family: museo-sans, sans-serif;
  letter-spacing: 0px;
  color: #012F57;
  opacity: 0.7;
  margin-right: 0.3125rem;
  line-height: 1.875rem;
`
const ExpandButton = styled.div`
  width: max-content;
  flex-direction: row;
  margin-top: 10px;
  cursor: pointer;
  &:hover ${StyledIcon} {
    color: #00afd9;
  }
`

const StyledGradient = styled.div`
    width: 100%;
    height: 4.7rem;
    transform: matrix(-1, 0, 0, -1, 0, 0);
    background: transparent linear-gradient(180deg, #FFFFFF 0%, #FFFFFFFE 21%, #FFFFFFEF 48%, #FFFFFFA5 76%, #FFFFFF00 100%) 0% 0% no-repeat padding-box;
    opacity: 1;
    bottom: 1.5rem;
    left: 0;
    position: absolute
`
const StyledGradientEx = styled.div`
    width: 100%;
    height: 2rem;
    transform: matrix(-1, 0, 0, -1, 0, 0);
    background: transparent linear-gradient(180deg, #FFFFFF 0%, #FFFFFFFE 21%, #FFFFFFEF 48%, #FFFFFFA5 76%, #FFFFFF00 100%) 0% 0% no-repeat padding-box;
    opacity: 1;
    bottom: 1.5rem;
    left: 0;
    position: absolute
`
const ContainerScroll = styled.div`
    ::-webkit-scrollbar {
      -webkit-appearance: none;
      width: 0.8125rem;
      background-color: rgba(237, 237, 237, 0.5);
      border-radius: 6.25rem;
    }
    
    ::-webkit-scrollbar-thumb {
      border-radius: 4px;
      border-radius: 6.25rem;
      background-color: #EFEFEF;
      box-shadow: 0 0 1px rgba(255, 255, 255, .5);
    }
`
export type Props = {
  longtext: boolean
}
export const ExpandableParagraph: React.FC<Props> = (props) => {
  const { children } = props
  const [expanded, changeExpanded] = React.useState(false)
  const [truncated, changeTruncated] = React.useState(false)
  const handleTruncate = (data: boolean) => {
    if (data !== truncated) {
      changeTruncated(truncated)
    }
  }
  if (props.longtext) {
    return (
      <div style={{ position: 'relative' }}>
        {!expanded && (
          <StyledGradient />
        )}
        {expanded && (
          <StyledGradientEx />   
        )}
        <StyledDes
          lines={!expanded && 5}
          ellipsis={(
            <ExpandButton 
              onClick={() => { changeExpanded(true) }}
            >
              <StyledExpand>
                Expand
              </StyledExpand>
              <StyledIcon icon={faPlus} />
            </ExpandButton>
          )}
          onTruncate={handleTruncate}
        >
          <div style={{ display: 'flex', height: '180px', width: '100%' }}>
            <ContainerScroll style={{ flex: 1, minWidth: 0, minHeight: 0, overflowY: 'scroll', overflowX: 'hidden' }}>
              <CustomScroll
                allowOuterScroll={true}
              >
                <div style={{ marginRight:'20px' }}>
                  {children}
                </div>
              </CustomScroll>
            </ContainerScroll>
          </div>
        </StyledDes>
        {!truncated && expanded && (
          <ExpandButton
            onClick={() => { changeExpanded(false) }} 
          >
            <StyledExpand>
              Minimise
            </StyledExpand>
            <StyledIcon icon={faMinus} />
          </ExpandButton>
        )}
      </div>
    )
  } else {
    return (
      <div style={{ position: 'relative' }}>
        <StyledDesNoExpand
        >
          {children}
        </StyledDesNoExpand>
      </div>
    )
  }
}

export default ExpandableParagraph
