export const renderDate = (input : string) => {
    let array = input.split('');
    let result = '';
    if (input.length === 3) {
        array.splice(2, 0, ' / ');
        result = array.join('');
        return result;
    } 
    if (input.length === 4) {
        array.splice(3, 0, '/ ');
        result = array.join('');
        return result;
    }
    if (input.length === 5) {
        array.splice(4, 0, ' ');
        result = array.join('');
        return result;
    }
    if (input.length === 8) {
        array.splice(7, 0, ' / ');
        result = array.join('');
        return result;
    }
    if (input.length === 9) {
        array.splice(8, 0, '/ ');
        result = array.join('');
        return result;
    }
    if (input.length === 10) {
        array.splice(9, 0, ' ');
        result = array.join('');
        return result;
    }
}


export const renderDateMobile = (input : string) => {
    let yyyy = input.substr(0, 4);
    let mm = input.substr(5,2);
    let dd = input.substr(8,2);
    let result = dd + "/" + mm + "/" + yyyy;
    return result;
}
