import React, { useState, useEffect, forwardRef } from 'react'

import styled from 'styled-components'

import { renderDateMobile } from './DateRenderer'
import { getMessageByName } from '../dataProcessing'
import { inputNumberOnly } from './InputNumberOnly'
import ValidationIndicator from '../ValidationIndicator'
import Tooltip from '../Tooltip'
import {
  validatePhoneNumber, validateEmail, validateDate,
  validatePostcode, handlePhoneNumber, handleDateOnDesktop, handleCreditCardNumber, 
  handleBankBSBNumber, handleBankAccountNumber, validateExpiryDate, 
  validateCreditCard, validateBSB, validateAccountNumber, handleExpiryDate
} from './utils'
import color from '../color-css/color'
import tooltipData from './tooltip.json'
import img from '../../assets/images/hand-point-up-1.png'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'

import {
  BANKACCOUNT_PAYMENT_ACCOUNT_NAME,
  BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER,
  BANKACCOUNT_PAYMENT_BSB,
  BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION,
  BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION,
  CREDITCARD_PAYMENT_DATE_OF_DEDUCTION,
  CREDITCARD_PAYMENT_FREQUENCY_OF_DEDUCTION,
  CREDITCARD_PAYMENT_CARD_HOLDER_FIRST_NAME,
  CREDITCARD_PAYMENT_CARD_HOLDER_LAST_NAME,
  CREDITCARD_PAYMENT_CARD_HOLDER_CARD_NUMBER,
  CREDITCARD_PAYMENT_CARD_HOLDER_EXPIRY_DATE,
  CREDITCARD_PAYMENT_CARD_HOLDER_CVC_CVV,
  BILLING_FIRST_NAME,
  BILLING_LAST_NAME,
  BILLING_ADDRESS,
  BILLING_UNIT_NUMBER,
  BILLING_CITY,
  BILLING_STATE_OR_TERITORY,
  BILLING_POSTCODE,

} from '../FormHighOrder/types/YourContributionField'

export type Props = {
  placeholder: string,
  widthSize?: string,
  name: string,
  maxLength?: number,
  type?: string,
  onInputSucessCallback?: (isSuccess: boolean) => void,
  onVisibility?: (isVisible: boolean) => void,
  onShow?: (isShow: boolean) => void,
  inputFromProps?: string,
  value?: string,
  onTypeText?: (textInputValue: string) => void,
  readOnly?: boolean,
  paddingSize?: string,
  clickedProp?: boolean,
  isValidated?: boolean, 
  onInputValueCallback?: (inputValue: string) => void,
}

interface InputField {
  widthSize?: string,
  placeholder: string,
  name: string,
  onVisibility?: (isVisible: boolean) => boolean,
  onShow?: (isShow: boolean) => boolean,
  maxLength?: number,
  type?: string,
  onInputSucessAsBackgroundColor: boolean,
  inputFromProps?: string,
  value?: string,
  onTypeText?: (textInputValue: string) => void
  readOnly?: boolean
  paddingSize?: string,
}

const InputField = styled.input<InputField>`
  font-family: museo-sans, sans-serif;
  @media screen and (max-width: 1024px) {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-left: 0.9375rem;
  }

  width: ${props => props.widthSize ? props.widthSize : "15.563rem"};
  height: 1.063rem;
  background-color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue : color.grey};
  transition: background 0.5s ease-in-out;
  border: 1px solid ${color.white};
  outline: none;
  color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue_17_percent : color.grey_dark};
  font-size: 1rem;
  padding: ${props => props.paddingSize ? props.paddingSize : "0.8125rem 1.625rem 0.75rem 1.5625rem"};
  border-radius: 0.625rem;  
  ::placeholder,
  ::-webkit-input-placeholder {
    color: ${color.grey_darker};
    font-family: museo-sans, sans-serif;
    font-size: 1rem;
  }
  &:focus {
    ::-webkit-input-placeholder { color:transparent; }
    ::-moz-placeholder { color:transparent; }
    border: 1px solid ${color.blue_darker};
  }
  
`
interface IHoverWrapper {
  hoverIcon: string
}
const HoveringWrapper = styled.div<IHoverWrapper>`
  margin-top: 1.25rem;
  @media screen and (max-width: 1024px) {
    margin-top: 0.9375rem;
  }
  &:hover ${InputField} {
    transition: border 0.5s ease-in-out;
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
    cursor: pointer;
  }
`

export type Ref = HTMLInputElement

const TextInput = forwardRef<Ref, Props>((props: any, aRef: any) => {
  const [inputSuccess, setInputSuccess] = useState(false);
  const [visibility, setVisibility] = useState(false);
  const [textOutput, setTextOutput] = useState('');
  const [clicked, setClicked] = useState(false);
  const [labelIconState, setLabelIconStateState] = useState(true)
  const { isValidated, inputFromProps,
      onShow: onClickedCallback, clickedProp,
       onInputSucessCallback, onInputValueCallback, onTypeText } = props

  useEffect(() => {
    if(inputFromProps !== undefined && isValidated !== false) {
      setTextOutput(inputFromProps)
      setClicked(false)
      setInputSuccess(isValidated)
      setVisibility(isValidated)
    }
  }, [inputFromProps, isValidated]);

  useEffect(() => {
    onTypeText ? onTypeText(textOutput) : {}
    if(inputSuccess === true) {
      onInputSucessCallback ? onInputSucessCallback(inputSuccess) : {}
      onInputValueCallback ? onInputValueCallback(textOutput) : {}
    }
    else{
      onInputSucessCallback ? onInputSucessCallback(inputSuccess) : {}
    }
  }, [inputSuccess, textOutput])

  useEffect(() => {
    onClickedCallback? onClickedCallback(clicked) : {}
  }, [clicked]);

  useEffect(() => {
    setClicked(false)
  }, [clickedProp]);

  useEffect(() => {
    if(visibility === true && inputSuccess === false) {
      setLabelIconStateState(false)
    } else {
      setLabelIconStateState(true)
    }
  },[inputSuccess, visibility])

  const handleMasking = (event: any) => {
    let inputGetter: string = event.target.value

    if (props.name === 'phoneNumber') {
      let phoneNumber = handlePhoneNumber(inputGetter, event)
      setTextOutput(phoneNumber)
    }
    else if (props.name === 'dateOnDesktop') {
      let dateOnDesktop = handleDateOnDesktop(inputGetter, event)
      setTextOutput(dateOnDesktop)
    }
    else if (props.name === 'dateOnMobile') {
      renderDateMobile(inputGetter)
    }
    else if (props.name === 'workSuburb') {
      inputGetter.length >= 3 ? setVisibility(true) : setVisibility(false);
      setTextOutput(inputGetter);
    }
    else if (props.name === 'address') {
      inputGetter.length >= 2 ? setClicked(true) : setClicked(false);
      setTextOutput(inputGetter);
    }
    else if (
      props.name === 'creditCard' ||
      props.name === CREDITCARD_PAYMENT_CARD_HOLDER_CARD_NUMBER
    ) {
      let creditCardNumber = handleCreditCardNumber(inputGetter, event)
      setTextOutput(creditCardNumber)
    }
    else if (
      props.name === 'expiryDate' ||
      props.name === CREDITCARD_PAYMENT_CARD_HOLDER_EXPIRY_DATE
    ) {
      let expiryDate = handleExpiryDate(inputGetter, event)
      setTextOutput(expiryDate)
    }
    else if (props.name === BANKACCOUNT_PAYMENT_BSB) {
      let bankAccountBSB = handleBankBSBNumber(inputGetter, event)
      setTextOutput(bankAccountBSB)
    }
    else if (props.name === BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER) {
      let bankAccountNumber = handleBankAccountNumber(inputGetter, event)
      setTextOutput(bankAccountNumber)
    }
    else {
      setTextOutput(inputGetter);
    }
  }

  const onSubmit = (event: any) => {
    let inputGetter = event.target.value;
    if (props.name === 'dateOnDesktop') {
      const unformatDate = inputGetter.split(' ').join('');
      setInputSuccess(() => validateDate(unformatDate))
    }
    else if (props.name === 'dateOnMobile') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'email') {
      setInputSuccess(() => validateEmail(inputGetter))
    }
    else if (
      props.name === 'firstName' ||
      props.name === CREDITCARD_PAYMENT_CARD_HOLDER_FIRST_NAME ||
      props.name === BILLING_FIRST_NAME
    ) {
      setInputSuccess(() => !!inputGetter);
    }
    else if (
      props.name === 'lastName' ||
      props.name === CREDITCARD_PAYMENT_CARD_HOLDER_LAST_NAME ||
      props.name === BILLING_LAST_NAME
    ) {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'phoneNumber') {
      const unformatPhoneNumber = inputGetter.split(' ').join('');
      setInputSuccess(() => validatePhoneNumber(unformatPhoneNumber))
    }
    else if (props.name === 'workSuburb') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (
      props.name === 'postcode' 
      || props.name === BILLING_POSTCODE
      ) {
      setInputSuccess(() => validatePostcode(inputGetter))
    }
    else if (
      props.name === 'address' ||
      props.name === 'address'
    ) {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'employer') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'gender') {
      setTextOutput(inputFromProps)
      setInputSuccess(() => !!textOutput);
    }
    else if (
      props.name === 'creditCard' ||
      props.name === CREDITCARD_PAYMENT_CARD_HOLDER_CARD_NUMBER
    ) {
      const unformatCreditNumber = inputGetter.split(' ').join('');
      setInputSuccess(() => validateCreditCard(unformatCreditNumber))
    }
    else if (
      props.name === 'expiryDate' ||
      props.name === CREDITCARD_PAYMENT_CARD_HOLDER_EXPIRY_DATE
    ) {
      const unformatCreditNumber = inputGetter.split(' ').join('');
      setInputSuccess(() => validateExpiryDate(unformatCreditNumber))
    }
    else if (
      props.name === 'cvc' ||
      props.name === CREDITCARD_PAYMENT_CARD_HOLDER_CVC_CVV
    ) {
      setInputSuccess(() => inputGetter.length === 3)
    }
    else if (props.name === BANKACCOUNT_PAYMENT_ACCOUNT_NAME) {
      setInputSuccess(() => !!inputGetter)
    }
    else if (props.name === BANKACCOUNT_PAYMENT_BSB) {
      const unformatBSB = inputGetter.split(' ').join('');
      const BSBValue = unformatBSB.replace('-','')
      setInputSuccess(() => validateBSB(BSBValue))
    }
    else if (props.name === BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER) {
      const unformatAccountNumber = inputGetter.split(' ').join('');
      setInputSuccess(() => validateAccountNumber(unformatAccountNumber))
    }
    else if (props.name === 'employmentStatus') {
      setInputSuccess(() => !!inputGetter)
    }
    else if (props.name === 'unitNumber' || props.name === BILLING_UNIT_NUMBER) {
      setInputSuccess(true)
    }
    else if (
      props.name === 'city'
      || props.name === 'stateOrTerritory'
      || props.name === BANKACCOUNT_PAYMENT_DATE_OF_DEDUCTION
      || props.name === BANKACCOUNT_PAYMENT_FREQUENCY_OF_DEDUCTION
      || props.name === CREDITCARD_PAYMENT_DATE_OF_DEDUCTION
      || props.name === CREDITCARD_PAYMENT_FREQUENCY_OF_DEDUCTION
      || props.name === BILLING_CITY
      || props.name === BILLING_STATE_OR_TERITORY
      || props.name === BILLING_POSTCODE
      || props.name === BILLING_ADDRESS
    ) {
      setInputSuccess(() => !!inputGetter)
    }

    setVisibility(true)
  }

  const onlyNumberKey = (event: any) => {
    if (props.name === 'creditCard'
      || props.name === 'phoneNumber'
      || props.name === 'expiryDate'
      || props.name === 'dateOnDesktop'
      || props.name === 'cvc'
      || props.name === 'bsb'
      || props.name === 'accountNumber'
      || props.name === 'postcode'
      || props.name === BANKACCOUNT_PAYMENT_BSB
      || props.name === BANKACCOUNT_PAYMENT_ACCOUNT_NUMBER
      || props.name === CREDITCARD_PAYMENT_CARD_HOLDER_CARD_NUMBER
      || props.name === CREDITCARD_PAYMENT_CARD_HOLDER_EXPIRY_DATE
      || props.name === CREDITCARD_PAYMENT_CARD_HOLDER_CVC_CVV
      || props.name === BILLING_POSTCODE
      || props.name === 'unitNumber'
      || props.name === BILLING_UNIT_NUMBER
    ) {
      inputNumberOnly(event)
    }
  }

  const onClickCallbackListener = () => {
    if (props.name === 'gender'
      || props.name === 'stateOrTerritory'
      || props.name === 'employmentStatus'
      || props.name === 'paymentCreditCard_DateOfDeduction'
      || props.name === 'paymentCreditCard_FrequencyOfDeduction'
      || props.name === 'paymentBankAccount_DateOfDeduction'
      || props.name === 'paymentBankAccount_FrequencyOfDeduction') {
      setClicked(!clicked)
    }
  }

  const { message = {}, label, tooltipName, labelNotRequire } = props
  const msg = getMessageByName(message[props.name], inputSuccess)

  let widthSize = ''
  let tooltipTitle = ''
  let tooltip_message = ''

  if (
    tooltipName === 'cvc' ||
    tooltipName === CREDITCARD_PAYMENT_CARD_HOLDER_CVC_CVV
  ) {
    widthSize = tooltipData.cvc[0].widthSize
    tooltipTitle = tooltipData.cvc[0].tooltipTitle
    tooltip_message = tooltipData.cvc[0].tooltip_message
  }

  return (
    <>
      {labelNotRequire && <Label>{labelNotRequire}</Label>}
      {label &&
        <Label>{label}
          <LabelIcon iconStates={labelIconState}>{'  '}*</LabelIcon>
        </Label>}
      <HoveringWrapper
        hoverIcon={img}>
        <InputField
          onChange={event => handleMasking(event)}
          onBlur={event => {
            onSubmit(event)
          }}
          placeholder={props.placeholder}
          widthSize={props.widthSize}
          paddingSize={props.paddingSize}
          name={props.name}
          onVisibility={
            props.onVisibility ? props.onVisibility(visibility) : {}
          }
          type={props.type}
          maxLength={props.maxLength}
          onInputSucessAsBackgroundColor={inputSuccess}
          value={textOutput}
          onClick={(_: any) => {
            onClickCallbackListener()
          }}
          onKeyPress={event => onlyNumberKey(event)}
          readOnly={props.readOnly}
          autoComplete='off'
          ref={aRef}
        />
      </HoveringWrapper>
      {tooltipName && <Tooltip {...{ tooltipName, widthSize, tooltipTitle, tooltip_message, visibility }} />}
      {msg
        && <ValidationIndicator
          isCorrect={inputSuccess} {...{ message: msg, visibility }}
        />}
    </>
  )
})

export default TextInput