import data from './suburb.json'

export interface Suburb {
  name: string
  postcode: number
}
export interface SuburbSuggestion {
  suburbs: Array<Suburb>
}

const suggestionDto: SuburbSuggestion = JSON.parse(JSON.stringify(data))

const handleStateByPostcode = (postcode : any) => {
  if ((2000 <= postcode && postcode <= 2599) || (2619 <= postcode && postcode <= 2899) 
    || (2619 <= postcode && postcode <= 2899)) {
    return 'NSW'
  }
  else if ((2600 <= postcode && postcode <= 2618) || (2900 <= postcode && postcode <= 2920)) {
    return 'ACT'
  }
  else if (3000 <= postcode && postcode <= 3999) {
    return 'VIC'
  }
  else if (4000 <= postcode && postcode <= 4999) {
    return 'QLD'
  }
  else if (5000 <= postcode && postcode <= 5799) {
    return 'SA'
  }
  else if (6000 <= postcode && postcode <= 6797) {
    return 'WA'
  }
  else if (7000 <= postcode && postcode <= 7799) {
    return 'TAS'
  }
  else if (800 <= postcode && postcode <= 899) {
    return 'NT'
  }
}

export const getSuggestions = (value: string): string[] => {
  const inputValue = value.trim().toLowerCase();
  const inputLength = inputValue.length;
  const { suburbs } = suggestionDto
  let suburbNamesSuggestion: any = []
  suburbs.forEach(each => {
    let bool = each.name.toLowerCase().slice(0, inputLength) === inputValue
    if(bool == true) {
      suburbNamesSuggestion.push(each.name + ', ' + handleStateByPostcode(each.postcode))
    }
  })
  return inputLength === 0 ? [] : suburbNamesSuggestion
}


export const suggestionRule = (value: string) => value.trim().length > 2;
