import React, { useEffect, forwardRef } from 'react'
import styled from 'styled-components'
import animationData from './check-blue.json'
import Lottie from 'react-lottie'
import handPointUpIcon from '../../assets/images/hand-point-up-1.png'

export type Props = {
  idActive: number
  id: number
  changeIdActive: () => void
  title: string
  widthSize?: string
  clickableProp: boolean
  isDisable?: boolean
}

const handleClickableEl = (clickable: boolean) => {
  if (clickable)
    return `cursor: url(${handPointUpIcon}), auto;`
  return ""
    + "cursor: not-allowed;\n"
    + "pointer-events: none;"
}


interface StyledCounter {
  widthSize?: string
  hover: boolean
  isClickable: boolean
  isDisable?: boolean
}

const StepIndexWrapper = styled.a<StyledCounter>`
  width: ${props => props.widthSize ? props.widthSize : '7.5rem'};
  margin: auto;
  display: flex;
  cursor: ${props => props.hover ? 'url(' + handPointUpIcon +'), auto' : 'pointer'};
  ${({ isClickable }) => handleClickableEl(isClickable)};
  @media screen and (max-width: 768px) {
    width: 28px;
  }
  pointer-events: ${props => props.isDisable ? '' : 'none'};
`

const StyledNum = styled.div`
  width: 1.75rem;
  margin-right: 0.5rem;
`
interface StyledNumSpanInterface {
  idNumber: number,
  idActive: number,
  hover: boolean,
}
// @ts-ignore
const StyledNumSpan = styled.span<StyledNumSpanInterface>`
  width: 1.625rem;
  height: 1.625rem;
  line-height: 1.625rem;
  font-size: 1rem;
  text-align: center;
  color: ${props => props.idNumber <= props.idActive ? '#00AFD9' : props.hover ? '#00AFD9' : '#757575'};
  background: ${props => props.idNumber <= props.idActive ? '#E2F1F8' : props.hover ? '#E2F1F8' : '#F3F3F3'};
  border-radius: 0.875rem;
  border: 1px solid #e2f1f8;
  display: block;
  font-weight: 600;
  font-size: 1rem;
  font-family: museo-sans, sans-serif;
 `
const StyledDesc = styled.div`
  font-size: 1rem;
  line-height: 1.75rem;
  color: #012e56;
  flex:1,
`
interface StyledTitleInterface {
  idNumber: number,
  idActive: number,
  hover: boolean,
  isMobile: boolean
}

const StyledTitle = styled.span<StyledTitleInterface>`
  text-align: left; 
  font-size: 1.15rem; 
  font-family: museo-sans ,sans-serif; 
  font-weight: ${props => props.idNumber < props.idActive ? 900 : 500};
  letter-spacing: -1.05px;
  color: ${props => props.idNumber <= props.idActive ? '#012F57' : props.hover ? '#012F57' : '#757575'}};
  display: inline;
  @media screen and (max-width: 768px) {
    display: none;
  }
  
`

const defaultOptions = {
  loop: false,
  autoplay: true,
  animationData: animationData,
}

export const ItemStep: React.FC<Props> = (props) => {
  const { idActive, id, changeIdActive, title, widthSize, clickableProp, isDisable } = props
  const [hover, changeHover] = React.useState(false)
  const [isMobile, changeIsMobile] = React.useState(false)
  const [isStopped, changeIsStopped] = React.useState(false)

  useEffect(() => {
    changeIsMobile(typeof window !== 'undefined' && window.innerWidth < 768 ? true : false)
    // @ts-ignore
    window.addEventListener('resize', changeIsMobile(typeof window !== 'undefined' && window.innerWidth < 768 ? true : false))


    // @ts-ignore
    return window.removeEventListener('resize', changeIsMobile(typeof window !== 'undefined' && window.innerWidth < 768 ? true : false))
  }, [])

  return (
    <StepIndexWrapper
      // @ts-ignore
      onClick={() => changeIdActive()}
      onMouseEnter={() => changeHover(true)}
      onMouseLeave={() => changeHover(false)}
      widthSize={widthSize}
      hover={hover}
      isClickable={clickableProp}
      isDisable={isDisable}
    >
      <StyledNum>
        <div style={{ height: 28, width: 28, position: 'relative' }}>
          {
            idActive > id ? (
              <Lottie
                options={defaultOptions}
                height={28}
                width={28}
                isStopped={isStopped} />
            )
              : (
                <StyledNumSpan
                  idActive={idActive}
                  idNumber={id}
                  hover={hover} >
                  {id + 1}
                </StyledNumSpan>
              )}

          <div style={{ position: 'absolute', top: 0 }}>
            {
              hover ? (
                <StyledNumSpan
                  idActive={idActive}
                  idNumber={id}
                  hover={hover} >
                  {id + 1}
                </StyledNumSpan>
              )
                :
                null
            }
          </div>
        </div>
      </StyledNum>
      <StyledDesc>
        <StyledTitle
          //@ts-ignore
          idActive={idActive}
          idNumber={id}
          hover={hover}
          isMobile={isMobile}
        >
          {title}
        </StyledTitle>
      </StyledDesc>
    </StepIndexWrapper>
  )
}

export default ItemStep
