import React, { useState, forwardRef, useEffect } from "react";
import styled from 'styled-components';
import color from '../color-css/color'
import usePlacesAutocomplete, { getGeocode, getZipCode  } from "use-places-autocomplete";
import ValidationIndicator from "../ValidationIndicator";
import img from '../../assets/images/powered_by_google_on_white.png'
import useOnclickOutside from "react-cool-onclickoutside"
import { handleStateOrTerritory } from './helper'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'

export type Props = {
  onSelectedItemProp?: (wasSelected: boolean) => void,
  inputFromProps?: string,
  isValidated?: boolean,
  handleAddressInfo?: (cityValue: string, state: string) => void
}

interface InputField {
  onInputSucessAsBackgroundColor: boolean
  onInputSucessListener?: (isSucess: boolean) => boolean,
}

const InputField = styled.input<InputField>`
  font-family: museo-sans, sans-serif;
  @media screen and (max-width: 1024px) {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-left: 0.9375rem;
  }
  width: 34rem;
  height: 1.1875rem;
  background-color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue : color.grey};
  transition: background 0.5s ease-in-out;
  border: 1px solid ${color.white};
  outline: none;
  color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue_17_percent : color.grey_dark};
  font-size: 1rem;
  padding: 0.75rem 5.375rem 0.6875rem 1.5625rem;
  border-radius: 0.625rem;  
  ::placeholder,
  ::-webkit-input-placeholder {
    color: ${color.grey_darker};
    font-family: museo-sans, sans-serif;
    font-size: 1rem;
  }
  &:focus {
    ::-webkit-input-placeholder { color:transparent; }
    ::-moz-placeholder { color:transparent; }
    border: 1px solid ${color.blue_darker};
  }
  margin-left: -2px;
`

const HoveringWrapper = styled.div`
  margin-bottom: 0.625rem;
  margin-top: 1.25rem;
  &:hover ${InputField} {
    transition: border 0.5s ease-in-out;
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
    cursor: pointer;	
  }
`

interface DropdownContainer {
  isShow: boolean
}

const DropdownContainer = styled.div<DropdownContainer>`
  display: ${props => props.isShow ? 'block' : 'none'};
  position: relative;
  margin-top: 1.25rem;
  width: 40.9375rem;
  height: 14.0625rem;
  @media screen and (max-width: 1024px) {
    width: 100%;
    box-sizing: border-box;
  }
  background: ${color.white} 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 50px #012F571A;
  border-radius: 10px;
  &:before {
      content: "";
      position: absolute;
      left: 19.25rem;
      top: -1.25rem;
      width: 0;
      height: 0;
      border-top: 0.9375rem solid transparent;
      border-right: 0.9375rem solid #fff;
      border-bottom: 0.875rem solid transparent;
      -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
      transform: rotate(90deg);
`

const DropdownFooter = styled.div`
  position:absolute;
  bottom:0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  width: 90%;
  height: 3.75rem;
  margin: 0 1.75rem;
  border-top: 1px solid ${color.grey_dark};
`

const DropdownFooterContent = styled.div`
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  font-style: italic;
  font-size: 1rem;
  color: ${color.grey_darker};
  margin-top: 1rem;
`

const DropdownFooterImg = styled.span`
  margin-top: 1.1875rem;
`

const DropdownSuggestWrapped = styled.div`
  position: absolute;
  width: 31.3125rem;
  height: 8.3125rem;
  padding: 0 7.25rem 0 1.75rem;
  list-style-type: none;
  font-weight: 300;
  font-size: 1rem;
  line-height: .625rem;
  overflow: auto;
  margin-top: 2rem;
  ::-webkit-scrollbar {
      width: 1.8125rem;
    }
  ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #EFEFEF; 
  border-radius: 10px;
  }
  ::-webkit-scrollbar-thumb {
  background: #EFEFEF; 
  border-radius: 10px;
  }
  @media screen and (max-width: 1024px) {
    width: calc(100% - 1.1875rem); 
    padding-left: 1.1875rem;
    margin-top: 1.375rem;
  }
`

interface DropdownSuggestContent {
  value?: (input: string) => string,
}

const DropdownSuggestContentWrapped = styled.div<DropdownSuggestContent>`
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.5625rem;
  color: ${color.grey_darker};
  cursor: pointer;	
 
`

const DropdownSuggestContent = styled.span``

const DropdownSuggestContentKey = styled.div`
  margin-bottom: 0.875rem;
  &:hover ${DropdownSuggestContent} {
    color: ${color.blue_17_percent}
`

interface Wrapper {
  onInputSucessListener?: (isSucess: boolean) => boolean,
}
const Wrapper = styled.div<Wrapper>`
  display: block;
`


type Ref = HTMLInputElement

const PlacesAutocomplete = forwardRef<Ref, Props>((props: any, aRef) => {
  const { inputFromProps, isValidated, onSelectedItemProp, handleAddressInfo } = props
  const [inputSuccess, setInputSuccess] = useState(false);
  const [visibility, setVisibility] = useState(false);
  const [showingDropdown, setShowingDropdown] = useState(false);
  const [clickedOutsideState, setClickedOutside] = useState(false);
  const [validatorMsg, setValidatorMsg] = useState('')
  const [labelIconState, setLabelIconStateState] = useState(true)


  const {
    value,
    suggestions: { data },
    setValue
  } = usePlacesAutocomplete({
    requestOptions: {
      componentRestrictions: { country: 'au' },
    },
    debounce: 500,
  },
  );


  const handleInput = (e: any) => {
    setValue(e.target.value);
    e.target.value.length >= 2 ? setShowingDropdown(true) : setShowingDropdown(false);
  };

  const handleSelect = (description : string) => () => {
    let streetaddress = description.substr(0, description.indexOf(','));
    let streetState = description.replace(streetaddress, '')
    streetState = streetState.replace(',', '')
    streetState = streetState.substr(1, streetState.indexOf(',') - 1)
    let stateOrTerritory: any = handleStateOrTerritory(streetState.slice(-3))
    let city: any = streetState.replace(streetState.slice(-3), '')
    handleAddressInfo ? handleAddressInfo(city, stateOrTerritory) : {}
    setValue(streetaddress);
    setShowingDropdown(false)
  };

  const renderSuggestions = () =>
    data.map((suggestion) => {
      const {
        id
      } = suggestion;
      // get postcode from address (done)
      // const params = {
      //   address : suggestion.description
      // }
      // getGeocode(params)
      // .then((results) => getZipCode(results[0], false))
      // .then((zipCode) => {
      //   console.log("ZIP Code: ", zipCode);
      // })
      // .catch((error) => {
      //   console.log("Error: ", error);
      // });
      let findSuggestionIsAddress = suggestion.description.substring(0,1)
      if (!isNaN(Number(findSuggestionIsAddress.toString()))) {
        let stringToGoIntoTheRegex = value
        let regex = new RegExp("" + stringToGoIntoTheRegex + "", "gi")
        let suggestString = suggestion.description.match(regex)
        let parseSuggestStringToString = JSON.stringify(suggestString)
        parseSuggestStringToString = parseSuggestStringToString.replace('["', "");
        let suggestResult = parseSuggestStringToString.substring(0, parseSuggestStringToString.indexOf('"'))
        const suggestHighlight = suggestion.description.substring(0, suggestResult.length)
        const suggest = suggestion.description.substring(suggestResult.length, suggestion.description.length)
        return (
          <DropdownSuggestContentKey key={id} onClick={handleSelect(suggestion.description)}>
            <DropdownSuggestContent><strong>{suggestHighlight}</strong>{suggest}</DropdownSuggestContent>
          </DropdownSuggestContentKey>
        );
      } else {
        return
      }
    });

  const onValidate = (e: any) => {
    let inputGetter = e.target.value;
    setInputSuccess(() => !!inputGetter);
    setVisibility(true)
  }

  useEffect(() => {
    onSelectedItemProp ? onSelectedItemProp(inputSuccess) : {}
    inputFromProps ? setValue(inputFromProps) : {}
    inputSuccess ?
      setValidatorMsg("Thanks") : setValidatorMsg("Enter your address")
    if(isValidated && isValidated === true) {
      setInputSuccess(isValidated)
      setValidatorMsg("Thanks")
      setVisibility(isValidated)
    } else {
      setValue(value)
    }
  }, [inputSuccess, inputFromProps, isValidated])

  const handleClickOutsideRef = useOnclickOutside(() => {
    setShowingDropdown(false)
    setClickedOutside(!clickedOutsideState)
  });

  useEffect(() => {
    if(visibility === true && inputSuccess === false) {
      setLabelIconStateState(false)
    } else {
      setLabelIconStateState(true)
    }
  },[inputSuccess, visibility])

  return (
    <Wrapper>
      <Label>Home address <LabelIcon iconStates={labelIconState}>{'  '}*</LabelIcon></Label>
      <HoveringWrapper>
        <InputField
          name= "address"
          placeholder="Type your address here"
          value={value}
          onChange={handleInput}
          onBlur={event => onValidate(event)}
          onInputSucessAsBackgroundColor={inputSuccess}
          ref={aRef}
          type="text"
          onInputSucessListener={props.onInputSucessProp ? props.onInputSucessProp(inputSuccess) : {}}
        />
      </HoveringWrapper>
      <DropdownContainer
      isShow={showingDropdown}
      ref={handleClickOutsideRef}
      >
        <DropdownSuggestWrapped>
          <DropdownSuggestContentWrapped>
            {renderSuggestions()}
          </DropdownSuggestContentWrapped>
        </DropdownSuggestWrapped>
        <DropdownFooter>
          <DropdownFooterContent>Can’t find your address? Continue typing</DropdownFooterContent>
          <DropdownFooterImg><img src={img} /></DropdownFooterImg>
        </DropdownFooter>
      </DropdownContainer>
      {validatorMsg !== ""
        && <ValidationIndicator isCorrect={inputSuccess} {...{ message: validatorMsg, visibility }} />}
    </Wrapper>
  );
});

export default PlacesAutocomplete