import React, { useState, useEffect } from 'react'

import styled from 'styled-components'

import Label from '../Label'
import ExpandableParagraph from '../ExpandableParagraph'
import SummarySection from '../SummarySection'

import {
  PAY_DEDUCTION_PAYMENT_ELOTRONIC_SIGNATURE} from '../FormHighOrder/types/YourContributionField'

import color from '../color-css/color'

export type Props = {
  expandableParagraphIsLongTextProp: boolean
  expandableParagraphDescriptionTextProp: string
  setPayDeductionTabValidation?: (wasSuccessful: boolean) => void
  onPayDeductionTabData?: (data: any) => void
}
const Container = styled.div`
  animation-name: flickity;
  animation-duration: 1s;  
  animation-fill-mode: forwards;
  @keyframes flickity {
      0% {
          opacity:0;
      }
      100% {
          opacity:1;
      }
  };
  margin-top: 2.5rem;
  max-width: 38.75rem;
  @media (max-width: 1024px) {
    max-width: 100%;
  }
`

const SigningText = styled.div
  `
  text-size: 1rem; 
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  color: ${color.blue_dark};
  width: 545px;
  @media (max-width: 1024px) {
    width: 100%;
  }
`

const LabelIcon = styled.span`
  color: ${color.blue_darker};
`

export const PayDeductionTab: React.FC<Props> = ({
  expandableParagraphIsLongTextProp,
  expandableParagraphDescriptionTextProp,
  setPayDeductionTabValidation,
  onPayDeductionTabData
}) => {

  const [electricalSignature, setElectricalSignature] = useState(false)

  useEffect(() => {
    setPayDeductionTabValidation ? setPayDeductionTabValidation(electricalSignature) : {}
    
    onPayDeductionTabData? onPayDeductionTabData({
      [PAY_DEDUCTION_PAYMENT_ELOTRONIC_SIGNATURE]: {
        isValidated: true,
        value: electricalSignature ? "signed" : undefined,
    },
    }) : {}
  }, [electricalSignature])

  return (
    <Container>
      <Label> Payroll deduction agreement {' '}
        <LabelIcon>*</LabelIcon>
        <div
          style={{
            marginTop: `1.5rem`
          }}>
          <ExpandableParagraph
            longtext={expandableParagraphIsLongTextProp}
          >
            {expandableParagraphDescriptionTextProp}
          </ExpandableParagraph>
        </div>
      </Label>

      <div
        style={{
          marginTop: `2.5rem`
        }}>
        <SigningText> By signing this agreement, you agree to the Terms and Conditions of the Payroll Deduction Request and Service Agreement
              {' '}
          <LabelIcon>*</LabelIcon>
        </SigningText>
      </div>

      <div
        style={{
          marginTop: `2.5rem`
        }}>
        <button
          type="button"
          onClick={(_) => {
            setElectricalSignature(!electricalSignature)
          }}
        >Sign electronically * - Design and developed by atomix</button>
      </div>

      <SummarySection
        summaryTableDataProp={[{
          title: "Weekly debit amount:",
          value: "$20.40"
        }]}
        noteOnPaymentProp={[
          "Membership fees will be automatically deducted from your pay and will appear on your payslip",
          "If you don’t work any hours that pay cycle, you won’t be charged any fees",
          "Your membership can be cancelled at any time",
          "Membership fees are tax-deductible",
          "Transactions will appear on your statement as ‘SDA UNION’"
        ]}
      />

    </Container>
  )
}

export default PayDeductionTab
