import React from 'react'
import styled from 'styled-components'
import color from '../color-css/color'

const Container = styled.div<PropsActive>`
  background: ${props => props.active ? "#E2F1F8" : "#F3F3F3"};
  border-radius: 0.625rem;
  opacity: 1;
  width: 11.125rem;
  height: 3.375rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background 0.5s ease-in-out;
  transition: border 0.5s ease-in-out;
  border: 1px solid ${color.white};
  margin-right: 0.625rem;
  cursor: pointer;	
  @media screen and (max-width: 1024px) {
    width: 100%;
    height: 2.625rem;
  };
`
const RadioButtonContainerWrapper = styled.div`
  @media screen and (max-width: 1024px) {
    width: 100%;
  };
  &:hover ${Container} {
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
    cursor: pointer;	
  }
`
const StyledTitle = styled.div<PropsActive>`
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: -0.15px;
  text-align: center;
  color:${props => props.active ? "#052948" : "#757575"};
`
interface Props {
  active: boolean,
  onPress: any,
  title: string,
}
interface PropsActive {
  active: boolean
}
export const ButtonItem: React.FC<Props> = (props) => {
  return (
    <RadioButtonContainerWrapper>
      <Container
        active={props.active}
        onClick={() => { props.onPress() }}>
        <StyledTitle
          active={props.active}
        >
          {props.title}
        </StyledTitle>
      </Container>
    </RadioButtonContainerWrapper>
  )
}
export default ButtonItem
