import React, { forwardRef, useEffect, useReducer } from 'react'
import styled from 'styled-components'
import ItemStep from './itemStep'

export type Props = {
  onItemStepClickedListenerProp?: (step: number) => void
  activateItemProp: number
  isDisableItemClick?: boolean
  isResetIndex?: boolean
}

const FormStepContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding-left: 2rem;
  padding-right: 1.25rem;
  box-sizing: border-box;
  border-radius: 0.625rem;
  box-shadow: 0px 0.625rem 0.625rem #012F571A;
  border: 1px solid #fff;
  @media screen and (max-width: 768px) {
    width: 173px;
    height: 45px;
    padding: 0px;
  }
`

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-column-gap: 34px;
  @media screen and (max-width: 768px) {
    grid-column-gap: 10px;
  }
`

type Action = {
  type: string,
  payload: { [index: number]: boolean };
}

const reducerClickableIndex = (state: any, action: Action) => {
  switch (action.type) {
    case "itemAt" + 0:
      return {
        ...state,
        0: action.payload[0]
      };
    case "itemAt" + 1:
      return {
        ...state,
        1: action.payload[1]
      };
    case "itemAt" + 2:
      return {
        ...state,
        2: action.payload[2]
      };
    case "itemAt" + 3:
      return {
        ...state,
        3: action.payload[3]
      };
    case "setFalse":
      return {
        ...state,
        0: action.payload[0],
        1: action.payload[1],
        2: action.payload[2],
        3: action.payload[3]
      }
    default:
      return {
        ...state
      }
  }
}

const FormStep: React.FC<Props> = (props) => {

  const { onItemStepClickedListenerProp, activateItemProp, isDisableItemClick, isResetIndex } = props
  const [idActive, changeIdActive] = React.useState(0)

  const [clickableState, dispatch] = useReducer(reducerClickableIndex,
    { 0: true, 1: false, 2: false, 3: false })

  const activateItemAt = (index: number) => {
    changeIdActive(index)
    dispatch({
      type: "itemAt" + index,
      payload: {
        [index]: true
      }
    })
  }

  const resetIndex = () => {
    dispatch({
      type: "setFalse",
      payload: {
        [0]: true,
        [1]: false,
        [2]: false,
        [3]: false
      }
    })
  }
  
  useEffect(()=>{
    isResetIndex ? resetIndex() : {}
  }, [isResetIndex])

  useEffect(()=>{
      activateItemAt(activateItemProp)
  }, [activateItemProp])

  return (
    <FormStepContainer>
      <ItemStep
        idActive={idActive}
        id={0}
        title={'Your work'}
        changeIdActive={() => {
          changeIdActive(0)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(0) : {}
        }}
        clickableProp={clickableState[0]}
        isDisable={isDisableItemClick}
      />

      <ItemStep
        idActive={idActive}
        id={1}
        title={'About you'}
        changeIdActive={() => {
          changeIdActive(1)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(1) : {}
        }}
        clickableProp={clickableState[1]}
        isDisable={isDisableItemClick}
      />

      <ItemStep
        widthSize = '14rem'
        idActive={idActive}
        id={2}
        title={'Membership agreement'}
        changeIdActive={() => {
          changeIdActive(2)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(2) : {}
        }}
        clickableProp={clickableState[2]}
        isDisable={isDisableItemClick}
      />

      <ItemStep
        widthSize = '11.1875rem'
        idActive={idActive}
        id={3}
        title={'Your contribution'}
        changeIdActive={() => {
          changeIdActive(3)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(3) : {}
        }}
        clickableProp={clickableState[3]}
        isDisable={isDisableItemClick}
      />

    </FormStepContainer>
  )
}

export default FormStep
