import React from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSquare } from '@fortawesome/free-regular-svg-icons'
import { faCheck } from '@fortawesome/free-solid-svg-icons'
import color from '../color-css/color'

const ButtonContainer = styled.div<PropsActive>`
  background: ${props => props.active ? '#E2F1F8' : '#F3F3F3'};
  border-radius: 0.625rem;
  opacity: 1;
  width: ${props => props.widthSize ? props.widthSize : '13rem'};
  padding: ${props => props.padding ? props.padding : '1.1875rem 4.625rem 1.125rem 1.25rem'};
  border: 1px solid ${color.white};
  margin-right: 0.625rem;
  @media screen and (max-width: 1024px) {
      width: 100%;
      align-items: center;
      justify-content: flex-start;
      margin-right: 0px;
      margin-left: 0px;
      padding: 0.6875rem 0;
  };
  display: flex;
  flex-direction: row;
  cursor: pointer;	
`

const ButtonContainerHover = styled.div`
  &:hover ${ButtonContainer} {
    transition: border 0.5s ease-in-out;
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
    cursor: pointer;	
  };
  @media screen and (max-width: 1024px) {
    width: 100%;
  };
`

const StyledTitle = styled.div<PropsActive>`
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: -0.05px;
  text-align: center;
  color:${props => props.active ? '#052948' : '#757575'};
`
const StyledIcon = styled(FontAwesomeIcon)<PropsActive>`
  color: ${props => props.active ? '#ff6820' : '#757575'};
  width: 1.25rem !important;
  height: 1.25rem;
  text-align: center;
  margin-right: 1.25rem;
  @media screen and (max-width: 1024px) {
    margin-left: 0.9375rem;
  }
`

interface Props {
  active: boolean
  onPress: any
  title: string
  padding?: string
  widthSize?: string
}
interface PropsActive {
  visibility?: boolean
  active: boolean
  padding?: string
  widthSize?: string
}
export const ButtonItem: React.FC<Props> = (props) => {
  const { padding, widthSize } = props
  return (
    <ButtonContainerHover>
      <ButtonContainer
        padding = {padding}
        widthSize = {widthSize}
        active={props.active}
        onClick={() => { props.onPress() }}>
        <StyledIcon
          active={props.active}
          icon={props.active ? faCheck : faSquare}
        />
        <StyledTitle active={props.active}>
          {props.title}
        </StyledTitle>
      </ButtonContainer>
    </ButtonContainerHover>
  )
}
export default ButtonItem