import React, { useState } from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSquare } from '@fortawesome/free-regular-svg-icons'
import { faCheck } from '@fortawesome/free-solid-svg-icons'
import ExpandableParagraph from '../ExpandableParagraph/'
import Tooltip from '../Tooltip'
import ValidationIndicator from '../ValidationIndicator'
import color from '../color-css/color'
export type Props = {
  variant_2?: boolean
  description: string
  description_2?: string
  longtext: boolean
  longtext_2?: boolean
  widthSize?: string
  padding?: string
  title?: string
  tooltip?: boolean
  validateMessage?: string
  checkboxContent?: string
  onCheckedValue?: (isChecked: boolean) => void
  inputFromProps?: boolean
}
interface ContainerCheckboxProps {
  isCheck?: boolean
  check: boolean
  widthSize?: string
  padding?: string
}
const ContainerCheckbox = styled.a<ContainerCheckboxProps>`
  width: ${props => props.widthSize ? props.widthSize : '133px'};
  background:${props => props.check ? '#E2F1F8' : '#EFF0F0'} ;
  transition: background 0.5s ease-in-out;
  border-radius: 0.625rem;
  opacity: 1;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  margin-top: 2.5rem;
  margin-bottom: 0.625rem;
  padding: ${props => props.padding ? props.padding : '18px 149px 17px 20px'};
  border: 1px solid ${color.white};
  cursor: pointer;	
  @media screen and (max-width: 1024px) {
    width: 100%;
    padding: 0.9375rem 0 13px 0;
  }
`
const ContainerCheckboxHover = styled.div`
  &:hover ${ContainerCheckbox} {
    transition: border 0.5s ease-in-out;
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
  }
`
const StyledIcon = styled(FontAwesomeIcon)<ContainerCheckboxProps>`
  width: 1rem;
  animation-name: flickity;
  animation-duration: 1s;  
  animation-fill-mode: forwards;
  @keyframes flickity {
      0% {
          opacity:0;
      }
      100% {
          opacity:1;
      }
  };
  display: ${props => props.isCheck ? 'block' : 'none'};
  color: ${props => props.check ? '#ff6820' : '#757575'};
  text-align: center;
  @media screen and (max-width: 1024px) {
    width: 0.875rem;
  }
`

const StyledTitle = styled.span<ContainerCheckboxProps>`
  text-align: left;
  font: 500 1rem museo-sans, sans-serif;
  letter-spacing: -0.05px;
  color: ${props => props.check ? color.blue_17_percent : '#757575'};
  opacity: 1;
  margin-left: 1.25rem;
`
const Container = styled.div`
  flex: 1;
  max-width: 38.75rem;
  @media screen and (max-width: 1024px) {
    max-width: 100%;
  }
`
const StyledTitleMain = styled.div`
  text-align: left;
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-family: museo-sans, sans-serif;
  letter-spacing: -0.05px;
  color: #012f57;
  opacity: 1;
  margin-bottom: 0.5rem;
  @media screen and (max-width: 1024px) {
    font-size: 1.0625rem;
    line-height: 1.75rem;
  }
`
const ContainerIcon = styled.div`
  width: 1rem;
  overflow: hidden;
  @media screen and (max-width: 1024px) {
    margin-left: 0.9375rem;
    width: 0.875rem;
  }
`

export const CheckboxField: React.FC<Props> = (props: any) => {
  const { title, title_2, tooltip, widthSize, padding,
    validateMessage, checkboxContent,
    onCheckedValue, inputFromProps
  } = props

  const [check, changeCheck] = useState(inputFromProps ? inputFromProps : false)

  React.useEffect(() => {
    onCheckedValue ? onCheckedValue(check) : {}
  }),[check]

  return (
    <Container>
      <div>
        <StyledTitleMain>
          {title}
          {' '}
          <span style={{ color: '#00AFD9', fontSize: '20px' }}>*</span>
        </StyledTitleMain>
        <ExpandableParagraph longtext={props.longtext}>
          {props.description}
        </ExpandableParagraph>
      </div>
      {props.variant_2 === true ? (
        <div>
          <StyledTitleMain>
            {title_2}
            {' '}
            <span style={{ color: '#00AFD9', fontSize: '20px' }}>*</span>
          </StyledTitleMain>
          <ExpandableParagraph longtext={props.longtext_2}>
            {props.description_2}
          </ExpandableParagraph>
        </div>
      ) : <></>
      }
      <ContainerCheckboxHover>
        <ContainerCheckbox
          widthSize={widthSize}
          padding={padding}
          check={check}
          onClick={() => {
            const checked = !check
            changeCheck(checked)
            onCheckedValue ? onCheckedValue(checked) : {}
          }}>
          <ContainerIcon>
            <StyledIcon
              isCheck={check ? true : false}
              check={check}
              icon={faCheck}
            />
            <StyledIcon
              isCheck={check ? false : true}
              check={check}
              icon={faSquare}
            />
          </ContainerIcon>
          <StyledTitle check={check}>
            {checkboxContent}
          </StyledTitle>
        </ContainerCheckbox>
      </ContainerCheckboxHover>
      {tooltip === true && (
        <Tooltip
          visibility={check}
          widthSize="418px"
          tooltipName="preExistingIssues"
          tooltipTitle="What does this mean?"
          tooltip_message="For example, if you acquired an injury at work 
        <span style='font-weight: bold; font-style: italic'>before</span> signing up with the SDA, we aren’t able to provide specific 
        assistance with that issue beyond general advice. However, if you acquired 
        an injury at work after signing up with the SDA, we’re here to help in any way that we can. "
        />
      )}
      {validateMessage && (
        <ValidationIndicator
          isCorrect={check}
          message={validateMessage}
          visibility={check} />
      )}
    </Container>
  )
}
export default CheckboxField