export const renderCreditCard = (input : string) => {
    let array = input.split('');
    let result = '';
    if (input.length === 5) {
        array.splice(4, 0, ' ');
        result = array.join('');
        return result;
    } else if (input.length === 10) {
        array.splice(9, 0, ' ');
        result = array.join('');
        return result;
    }
    else if (input.length === 15) {
        array.splice(14, 0, ' ');
        result = array.join('');
        return result;
    }
    else return input
}

export const renderExpiryDate = (input: string) => {
    let array = input.split('');
    let result = '';
    if (input.length === 3) {
        array.splice(2, 0, ' / ');
        result = array.join('');
        return result;
    }
    if (input.length === 4) {
        array.splice(3, 0, '/ ');
        result = array.join('');
        return result;
    }
    if (input.length === 5) {
        array.splice(4, 0, ' ');
        result = array.join('');
        return result;
    }
    else return input
}

export const renderBSB = (input: string) => {
    let array = input.split('');
    let result = '';
    if (input.length === 4) {
        array.splice(3, 0, ' - ');
        result = array.join('');
        return result;
    }
    if (input.length === 5) {
        array.splice(4, 0, '- ');
        result = array.join('');
        return result;
    }
    if (input.length === 6) {
        array.splice(5, 0, ' ');
        result = array.join('');
        return result;
    }
}

export const renderAccountNumber = (input : string) => {
    let array = input.split('');
    let result = '';
    if (input.length === 5) {
        array.splice(4, 0, ' ');
        result = array.join('');
        return result;
    } 
    else if (input.length > 5) {
        result = array.join('');
        return result;
    }
}