import styled from 'styled-components'
import color from '../color-css/color'
import React, {useEffect} from 'react'

export type Props = {
  iconStates?: boolean,
}

const handleButtonStates = (onState?: boolean) => {
  return onState && onState === true ? color.blue_darker : color.red
}

interface LabelIcon {
  onState?: boolean
}

const LabelIconStyle = styled.span<LabelIcon>`
  color: ${({ onState }) => handleButtonStates(onState)};
`
const LabelIcon: React.FC<Props> = (props:any) => {
  const {iconStates} = props
  const [validation, setValidation] = React.useState(iconStates)

  useEffect(() => { 
    setValidation(iconStates)
   }, [iconStates, validation])

  return (
    <>
      <LabelIconStyle onState={iconStates}> 
        {props.children}
      </LabelIconStyle>
    </>
  )
}

export default LabelIcon
