import React from 'react'
import styled from 'styled-components'
import ButtonItem from './ItemButton'
import Label from '../Label'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ValidationIndicator from '../ValidationIndicator/index'
import color from '../color-css/color'

const StyledIconChecked = styled(FontAwesomeIcon)`
  color: #189126;
  width: 1.25rem !important;
  height: 1.25rem;
  text-align: center;
`

const Container = styled.div`
  @media screen and (max-width: 1024px) {
    display: grid;
    grid-template-columns: auto;
    grid-row-gap: 0.625rem;
  }
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 1.25rem;
  margin-bottom: 0.6875rem;
`

interface RadioButtonContaiter {
  className?: string
  visibility?: boolean
}

const RadioButtonContaiter = styled.div<RadioButtonContaiter>`
  display: ${props => props.visibility === true ? 'block' : 'none'};
  @media (max-width: 1024px) {
    margin-top: 1.875rem;
  }
`

const LabelIcon = styled.span`
  color: ${color.blue_darker};
`

export type Props = {
  className?: string,
  onSelectTabProp?: (tabNumber: number) => void,
  label: string,
  title1: string,
  title2: string,
  title3: string,
  visibility?: boolean, 
  onSelectedValue?: (value: string) => void,
  inputFromProps?: string
}

export const RadioButtonGroup: React.FC<Props> = ({
  className, onSelectTabProp, label, title1, title2, title3, visibility, 
  onSelectedValue, inputFromProps
}) => {
  const [activeId, setActiveId] = React.useState(3)
  const [validatorMsg, setValidatorMsg] = React.useState("")

  React.useEffect(() => {
    if(inputFromProps === title1) {
      setActiveId(0)
      setValidatorMsg('Thanks')
    } else if(inputFromProps === title2){
      setActiveId(1)
      setValidatorMsg('Thanks')
    }
    else if(inputFromProps === title3){
      setActiveId(2)
      setValidatorMsg('Thanks')
    }
  }, [inputFromProps])

  return (
    <RadioButtonContaiter
      className={className}
      visibility={visibility}
    >
      <Label>{label}
        {' '}
        <LabelIcon>*</LabelIcon>
      </Label>
      <Container>
        <ButtonItem
          active={activeId == 0}
          title={title1}
          onPress={() => {
            setActiveId(0)
            onSelectTabProp ? onSelectTabProp(0) : {}
            onSelectedValue ? onSelectedValue(title1) : {}
          }} />
        <ButtonItem
          active={activeId == 1}
          title={title2}
          onPress={() => {
            setActiveId(1)
            onSelectTabProp ? onSelectTabProp(1) : {}
            onSelectedValue ? onSelectedValue(title2) : {}
          }} />
        <ButtonItem
          active={activeId == 2}
          title={title3}
          onPress={() => {
            setActiveId(2)
            onSelectTabProp ? onSelectTabProp(2) : {}
            onSelectedValue ? onSelectedValue(title3) : {}
          }} />
      </Container>
      <ValidationIndicator message={validatorMsg} isCorrect={true} visibility={activeId == 3 ? false : true} />

    </RadioButtonContaiter>
  )
}
export default RadioButtonGroup
