import React from 'react'
import styled from 'styled-components'

interface Spinner {
  showHide?: string,
}
export type Props = {
  className: string
}

const SpinnerWrapper = styled.div`
  width: 20px;
  height: 20px;
  animation: spin 2s linear infinite;
  &:before,
  &:after {
    content: '';
    position: absolute;
  }

  &:before {
    border-radius: 50%;
    background:
      linear-gradient(0deg,   rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.7) 100%)   0%   0%,
      linear-gradient(90deg,  rgba(255, 255, 255, 0.7)  0%, rgba(255, 255, 255, 1) 100%) 100%   0%,
      linear-gradient(180deg, rgba(255, 255, 255, 1)  0%, rgba(255, 255, 255, 1) 100%) 100% 100%,
      linear-gradient(360deg, rgba(255, 255, 255, 1)  0%, rgba(255, 255, 255, 1) 100%)   0% 100%
    ;
    background-repeat: no-repeat;
    background-size: 50% 50%;
    top: -1px;
    bottom: -1px;
    left: -1px;
    right: -1px;
  }

  &:after {
    background: #FF6820;
    border-radius: 50%;
    top: 8%;
    bottom: 8%;
    left: 8%;
    right: 8%;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }
`

export const Spinner: React.FC<Props> = ({ className }) => (
  <SpinnerWrapper className={className} role='progressbar' />
)

